import {useState, useEffect, useRef} from 'react';

import {Redirect, BrowserRouter as Router, Switch, Route} from 'react-router-dom'

import Fpage from './components/frontpage'
import Projects from './components/projects'

function App() {
  const scrollAmt = 4*54;
  const pageMax = 1;

  const [stage, setStage] = useState(0);
  const scroll = useRef(0);
  const [tempv , setTempv] = useState(true);


  //Changing State on scroll
  useEffect(()=>{
    if(scroll.current>scrollAmt){
      scroll.current = 0;
      if(stage<pageMax){
        setStage(stage+1);
      }
    }
    else if (scroll.current < -scrollAmt) {
      scroll.current = 0;
      if(stage>0){
        setStage(stage-1);
      }
    }
  },[tempv]);

  const handleScroll = (e) => {
      scroll.current += e.deltaY;

      //This if is used to trigger a page re-render only when scrolled past scrollAmt
      if( Math.floor(scroll.current/scrollAmt) ){
        setTempv(!tempv);
      }

  }
  return(
  <Router>
    <div className="App" onWheel={(e) => handleScroll(e)}>
      <RedirectFunction stage={stage} />
      <Switch>
        <Route path='/' exact>
          <Fpage />
        </Route>

        <Route path='/projects' exact>
          <Projects />
        </Route>

      </Switch>
    </div>
  </Router>
  );
}

export default App;


const RedirectFunction = ({stage}) => {
    switch (stage) {
      case 0:
        return <Redirect to='/' />
        break;
      case 1:
        return <Redirect to='/projects' />
        break;
      default:
        return <Redirect to='/' />
    }
}
